.new-expense {
  animation: anima 3s;
}
@keyframes anima {
  0% {
    margin-left: -300px;
    opacity: 0;
  }
  50% {
    opacity: 0.5px;
    margin-left: -40px;
  }
  100%{
    opacity: 1;
    margin-left: 0;
  }
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-expense__actions {
  text-align: right;
}
